.profile-picture {
    border-radius: 50%;
    width: 150px;
    margin: 10px; 
  }
  
  .profile-visual{
    flex-direction: column;
    margin: 50px 50px 20px 20px;
  }
  
  .profile-links-container{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
  }
  
  
  .fa {
    padding: 10px;
    font-size: 20px;
    margin: 2px;
    text-align: center;
    text-decoration: none;
    background: #4b4b4b;
    color: white;
  }
  .fa:hover {
    opacity: 0.7;
    color:black;
  }
  
  
  .profile-summary{
    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center; 
    margin: 50px 20px 20px 50px;
  }
  
  .profile-section{
    display: flex;
    justify-content: center; 
    align-items: center; 
  }
  
  
  .profile-title{
    font-size: xx-large;
    font-weight: bolder;
    border-bottom: 2px solid black;
  }
  .profile-subtitle{
    font-size: x-large;
    font-weight: lighter;
  }
  
  .profile-about{
    display: flex;
    align-items: center;
    justify-content: center; 
    padding: 20px 20% 20px 20%;
    text-align: justify;
    margin-bottom: 40px;
  }
  